* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  transition: all 0.4s ease-in-out;
}
li{
  list-style-type: none;
}
.page{
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content:space-between;
}
body{
  background-image: url(assets/bg.jpg);
  background-repeat: no-repeat;
  background-size:cover;  
}
.main{
  overflow: hidden;
  width: 100vw;
  height: 86vh;
}
.header, .footer{
  width: 100vw;
  height: 7vh;
  background-color: #121c1e;
}
.container{
    position: relative;
    width: 95vw;
    margin: 0 auto;
}
.wrapper, .wrapper-left{
  position: absolute;
  width: 95vw;
  height: 86vh;
  left:-200vh;
  transition: left 0.5s ease-in-out;
}
.active{
  left:0;
}